import { forwardRef } from "react";
import { FaCalendarAlt, FaMapMarkerAlt, FaRegClock } from "react-icons/fa";
import { Element } from "react-scroll";

import Col from "../../components/Col";
import Container from "../../components/Container";
import Maps from "../../components/Maps";
import Paper from "../../components/Paper";
import Row from "../../components/Row";

type Props = {
  children?: React.ReactNode;
};

const Schedule = forwardRef(
  ({ children }: Props, ref: React.Ref<HTMLDivElement>) => {
    const receptionLocation = {
      lat: -8.479291,
      lng: 118.769206,
      link: "https://goo.gl/maps/zrJProFCypaGMNiw5",
    };

    return (
      <Element name="Schedule">
        <Container fullWidth>
          <Row align="center">
            <Col size={12}>
              <h3 className="text-center">
                Save the <strong>date!</strong>
              </h3>
            </Col>
          </Row>
          <Row align="center" verticalAlign="top" noMargin>
            <Col size={4} mobile={10}>
              <Paper>
                <Row align="center" verticalAlign="verticalCenter" noMargin>
                  <Col size={12}>
                    <div>
                      <h2 className="text-center">Tasyakuran</h2>
                      <p className="text-center">
                        Acara <strong>tasyakuran aqiqah</strong> putra kami
                        insya Allah akan dilaksanakan pada:
                      </p>
                      <p className="text-left">
                        <FaCalendarAlt></FaCalendarAlt> <strong>Selasa</strong>,
                        8 Agustus 2023
                      </p>
                      <p className="text-left">
                        <FaRegClock></FaRegClock> 12.30 <strong>WITA</strong>{" "}
                        (Ba'da Dzuhur) - Selesai
                      </p>
                      <p className="text-left">
                        <FaMapMarkerAlt></FaMapMarkerAlt> Kediaman kami di Gang
                        Dahlia{" "}
                        <strong>(sebelah timur Alfamart Rabadompu)</strong>{" "}
                        no.28 RT 009 RW 003 Linkungan Lewiloa Rabadompu Timur,
                        Kota Bima
                      </p>
                    </div>
                  </Col>
                  <Col size={12}>
                    <Maps {...receptionLocation}></Maps>
                  </Col>
                </Row>
              </Paper>
            </Col>
          </Row>
        </Container>
      </Element>
    );
  }
);

export default Schedule;
